.add-card {
  border: 1px solid grey;
  border-radius: 4px;
  padding: 0.5rem 0.5rem;
  margin: 0.3rem 0;
  background-color: white;
  display: flex;
  flex-direction: column;
}

.task-container-add {
  padding: 1rem 0;
  background-color: white;
  border: 1px solid grey;
  border-radius: 4px;
}
.task-container-add:hover {
  cursor: pointer;
  background-color: var(--color-accent);
}

.addTask-close {
  position: absolute;
  top: 1rem;
  right: 1.2rem;
  border: none;
  padding: 0.44rem 0.44rem 0.4rem;
  margin-top: 0.6rem;
  border-radius: 25%;
  border: 1px solid black;
  background-color: white;
  font-size: 1.04rem;
}

.addTask-close:hover {
  background-color: var(--color-button-hover);
  cursor: pointer;
}

@media only screen and (min-width: 680px) {
  .add-card {
    padding: 0.7rem 0.7rem;
    margin: 0.42rem 0;
  }

  .addTask-close {
    right: 1.68rem;
    padding: 0.616rem 0.616rem 0.196rem;
    font-size: 1.456rem;
  }
}

@media only screen and (min-width: 1441px) {
  .add-card {
    padding: 0.98rem 0.98rem;
    margin: 0.588rem 0;
  }

  .addTask-close {
    right: 2.352rem;
    padding: 0.862rem 0.862rem 0.274rem;
    font-size: 2.038rem;
  }
}