.popup-inner.settings-popup {
  padding: 4rem 10rem;
}

.settings-header {
  margin: 0 0 2rem;
}

.settings-section {
  margin: 0;
  width: 100%;
  padding: 0.5rem 0.2rem;
  border-bottom: 1px solid black; 
  font-size: 2.2rem;
}

.settings-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.settings-item {
  display: flex;
  width: 100%;
  padding: 0.5rem;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px dotted black;
  border-left: 1px solid black;
  border-right: 1px solid black;
  background-color: white;
}

.settings-item.section-end {
  border-bottom: 1px solid black;
}

.settings-label {
  font-size: 1.5rem;
}


.settings-input {
  padding: 0 0.4rem;
  height: 3rem;
  width: 5rem;
  font-size: 1.5rem;
  text-align: center;
  font-family: 'Varela Round', sans-serif;
  border: none;
  background-color: var(--color-secondary);
  border-radius: 4px;

}

.settings-checkbox {
  cursor: pointer;
}


.settings-save {
  margin: 1.6rem 0 0;
  width: 100%;
  height: 2.4rem;
  font-family: 'Varela Round', sans-serif;
  border: none;
  background-color: white;
  font-size: 1.6rem;
  border-radius: 4px;
}

.settings-save:hover {
  cursor: pointer;
  background-color: var(--color-green);

}

.submit-error {
  color: orangered;
  margin: 0.5rem;
  font-size: 1.2rem;
  place-self: center;
}

@media only screen and (min-width: 680px) {
  .settings-save {
    margin: 2.24rem 0 0;
    height: 3.36rem;
    font-size: 2.24rem;
  }

  .submit-error {
    font-size: 1.68rem;
  }
}

@media only screen and (min-width: 1441px) {
  .settings-save {
    margin: 3.136rem 0 0;
    height: 4.704rem;
    font-size: 3.136rem;
  }

  .submit-error {
    font-size: 2.352rem;
  }
}